const askQuestion = async () => {
  if (!question.trim()) return;

  const userMessage = { type: 'question', text: question };
  setMessages((prev) => [...prev, userMessage]);
  setLoading(true);

  try {
    const res = await fetch('http://127.0.0.1:8000/api/ask-question/', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        document_id: parseInt(docId),
        question: question,
        chat_session_id: null, // optional for now
        top_k: 5
      }),
    });

    if (!res.ok) throw new Error('Failed to fetch answer');

    const data = await res.json();

    const botMessage = {
      type: 'answer',
      text: data.answer || 'No answer returned.',
    };

    setMessages((prev) => [...prev, botMessage]);
  } catch (err) {
    console.error(err);
    setMessages((prev) => [...prev, { type: 'answer', text: 'Error: Unable to get answer.' }]);
  } finally {
    setLoading(false);
    setQuestion('');
  }
};
